import { FunctionalComponent, h } from '@stencil/core';

export type FcInputCounterType = {
  accesibleLabel: string;
  value: string;
  maxlength: number;
};

const accesibleCounterLabel = (accesibleLabel: string, value: string, maxlength: number) => {
  let label = accesibleLabel;
  label = label.replace(/\[length\]/g, String(value.length));
  label = label.replace(/\[max\]/g, String(maxlength));
  label = label.replace(/\[rest\]/g, String(maxlength - value.length));

  return label;
};

export const FcInputCounter: FunctionalComponent<FcInputCounterType> = props => {
  return [
    <div class="ath-input__counter" aria-hidden="true">
      {!!props.value ? props.value.length : 0}
      {props.maxlength && <span>/{props.maxlength}</span>}
    </div>,
    <div class="ath-visibility-hidden" aria-live="polite">
      {accesibleCounterLabel(props.accesibleLabel, (props.value = ''), props.maxlength)}
    </div>,
  ];
};
